@import "../../styles/variable.scss";

.layout {
  display: flex;
  flex-direction: column;
  background-color: $darkBG;
  color: $lightBG;
  width: 100%;
  min-height: calc(100vh - 0px);
  .content .container {
    display: flex;
    align-items: center;
    padding: 40px;
    min-height: calc(100vh - 121px);
    .myInfo {
      width: 50%;
      h2 {
        font-size: 44px;
        font-family: $montserratXBold;
        &::after {
          content: " ";
          width: 15%;
          display: block;
          margin: 20px 0;
          border-bottom: 8px solid $primaryColor;
          @media only screen and (max-width: 1366px) {
            border-bottom: 2px solid $primaryColor;
            margin: 16px 0;
          }
          @media only screen and (max-width: 1024px) {
            width: 30%;
          }
          @media only screen and (max-width: 767px) {
            margin: 24px 0;
            border-bottom: 8px solid $primaryColor;
          }
        }
        @media only screen and (max-width: 1440px) {
          font-size: 40px;
        }
        @media only screen and (max-width: 1366px) {
          font-size: 36px;
        }
        @media only screen and (max-width: 1024px) {
          font-size: 32px;
        }
      }
      .subText {
        font-size: 20px;
        font-family: $montserratBold;
        @media only screen and (max-width: 1440px) {
          font-size: 18px;
        }
        @media only screen and (max-width: 767px) {
          font-size: 14px;
        }
      }
      @media only screen and (max-width: 1440px) {
        width: 55%;
      }
      @media only screen and (max-width: 1024px) {
        width: 70%;
      }
      @media only screen and (max-width: 767px) {
        width: 100%;
      }
    }
    .profile {
      width: 550px;
      height: 550px;
      margin: auto;
      flex-shrink: 0;
      .working {
        height: 100%;
        width: 100%;
        transform: rotateY(180deg);
      }
      @media only screen and (max-width: 1440px) {
        width: 450px;
        height: 450px;
      }
      @media only screen and (max-width: 1366px) {
        width: 350px;
        height: 350px;
      }
      @media only screen and (max-width: 1024px) {
        width: 400px;
        height: 400px;
        margin: 30px auto;
      }
      @media only screen and (max-width: 767px) {
        width: 300px;
        height: 300px;
        margin: 20px auto;
      }
    }
    @media only screen and (max-width: 1440px) {
      min-height: calc(100vh - 119px);
    }
    @media only screen and (max-width: 1366px) {
      min-height: calc(100vh - 116px);
    }
    @media only screen and (max-width: 1024px) {
      padding: 40px 30px;
      flex-direction: column;
      justify-content: center;
    }
    @media only screen and (max-width: 767px) {
      padding: 30px 20px;
    }
  }
  @media only screen and (max-width: 767px) {
    font-size: 20px;
  }
}

.container {
  max-width: 1500px;
  margin: auto;
}
