@import "./variable.scss";

@font-face {
  font-family: $montserrat;
  src: local($montserrat), url("../assets/fonts/Montserrat-Regular.ttf");
}
@font-face {
  font-family: $montserratThin;
  src: local($montserratThin), url("../assets/fonts/Montserrat-Thin.ttf");
}
@font-face {
  font-family: $montserratBold;
  src: local($montserratBold), url("../assets/fonts/Montserrat-SemiBold.ttf");
}
@font-face {
  font-family: $montserratXBold;
  src: local($montserratXBold), url("../assets/fonts/Montserrat-ExtraBold.ttf");
}
@font-face {
  font-family: $signatureFont;
  src: local($signatureFont), url("../assets/fonts/Signature.ttf");
}

body {
  font-family: $montserrat;
  background-color: $darkBG;
  color: $lightBG;
}

a {
  text-decoration: none;
  color: $lightBG;
  &:hover {
    color: $lightBG;
  }
}
