@import "../../styles/variable.scss";

.container {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  z-index: 1;
  background-color: transparent;
  pointer-events: none;
  transition: backdrop-filter 0.3s, background-color 0.3s;
  &.menuOverlayActive {
    position: fixed;
    backdrop-filter: blur(7px);
    background-color: rgba($color: $white, $alpha: 0.2);
    pointer-events: all;
  }
  .hamburgerMenu {
    position: relative;
    pointer-events: all;
    cursor: pointer;
    margin-top: 35px;
    width: 70px;
    margin-right: 20px;
    padding: 10px;
    transition: padding 0.3s;
    &::before {
      content: " ";
      display: block;
      border-bottom: 2px solid $lightBG;
      width: 100%;
      margin-bottom: 10px;
      border-top: 2px solid $lightBG;
      padding-top: 10px;
      transition: 0.3s;
    }
    &::after {
      content: " ";
      display: block;
      border-bottom: 2px solid $lightBG;
      width: 100%;
      margin-bottom: 0;
      transition: 0.3s;
    }
    &.isOpen {
      padding: 20px 10px;
      &::before {
        transform: rotateZ(45deg);
        margin-bottom: 0;
        border-top: 0;
        padding-top: 0;
      }
      &::after {
        margin-bottom: 0;
        transform: rotateZ(-45deg) translate(1px, -2px);
      }
    }
    @media only screen and (max-width: 1440px) {
      width: 60px;
    }
    @media only screen and (max-width: 1366px) {
      margin-top: 32px;
    }
    @media only screen and (max-width: 1024px) {
      width: 55px;
      margin-top: 30px;
      &::before {
        margin-bottom: 8px;
        padding-top: 8px;
      }
    }
    @media only screen and (max-width: 767px) {
      width: 45px;
      margin-top: 25px;
      margin-right: 10px;
      &::before {
        margin-bottom: 6px;
        padding-top: 6px;
      }
    }
  }
  .menu {
    right: 0;
    max-width: 0px;
    width: fit-content;
    height: 100vh;
    background: $dark;
    overflow: hidden;
    transition: max-width 0.3s;
    pointer-events: all;
    .menuList {
      padding: 40px 25px;
      margin: 0;
    }
    .menuItem {
      display: list-item;
      list-style: none;
      padding: 10px 30px;
      margin-bottom: 25px;
      cursor: pointer;
      background-color: transparent;
      transition: background-color 0.3s;
      min-width: 350px;
      text-align: center;
      font-size: 24px;
      letter-spacing: 4px;
      font-family: $montserratBold;
      &.active {
        color: $primaryColor;
        &:hover {
          color: $primaryColor;
        }
        &::after {
          border-color: $primaryColor;
        }
      }
      &:hover {
        background-color: rgba($color: $white, $alpha: 0.1);
        &::after {
          width: 20%;
          border-width: 1px;
          margin: 8px auto 0 auto;
        }
      }
      &::after {
        content: " ";
        display: block;
        border-bottom: 4px solid $lightBG;
        width: 10%;
        margin: 5px auto 0 auto;
        transition: width 0.3s, border 0.3s, margin 0.3s;
      }
      @media only screen and (max-width: 767px) {
        min-width: unset;
        letter-spacing: 1.2px;
        font-size: 20px;
        font-family: $montserrat;
        margin-bottom: 15px;
        padding: 5px 30px;
        &::after {
          border-width: 1px;
          width: 30%;
          margin-top: 3px;
        }
        &:hover {
          &::after {
            width: 60%;
            margin-top: 3px;
          }
        }
      }
    }
    &.openMenu {
      max-width: 400px;
      @media only screen and (max-width: 767px) {
        max-width: 250px;
      }
    }
  }
}
